.header {
  text-align: center;
  background-color: #f4b319;
  padding: 16px 32px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
  color: white;
  font-size: 25px;
  font-family: "McLaren", cursive;
}

.search-bar {
  margin-top: -35px;
  margin-bottom: 25px;
  text-align: center;
}

.cards-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
}

.card {
  border: none !important;
}

.card-title.h5 {
  font-size: 15px;
}

p.card-text {
  font-style: italic;
  font-size: 12px;
}

.card-img-top {
  margin: auto;
  width: 65%;
  height: 15vw;
  object-fit: cover;
}
.btn {
  margin-top: -5px;
  margin-left: 5px;
  height: 35px;
  padding-top: 4px !important;
}

input {
  width: 300px;
  height: 38px;
  border-radius: 5px;
  margin-left: -190px !important;
}

.list {
  margin-left: 10px;
  margin-top: 15px;
}

.list-counter {
  margin: 5px;
}

.book-list {
  float: left;
  height: 100vh;
  width: 200px;
}
